
.smart-grid-pagination{font-size:12px;margin-top:10px
}
.smart-grid-pagination select{padding:3px 6px;text-align:center;margin:0 5px;outline:0
}
.smart-grid-pagination .total{color:#f33;padding:0 5px
}
.smart-grid-pagination .pages{display:inline-block
}
.smart-grid-pagination .pages li{display:inline-block;margin:2px
}
.smart-grid-pagination .pages li button{outline:0;padding:0;cursor:pointer;display:inline-block;border:1px solid #e5e5e5;background-color:#fafafa;width:22px;height:22px;line-height:22px;text-align:center;font-size:14px;color:#666;transition:all .3s
}
.smart-grid-pagination .pages li button[disabled]{opacity:.6;cursor:not-allowed
}
.smart-grid-pagination .pages li button:hover:not([disabled]){border-color:#f24f44;color:#f24f44
}
.smart-grid-pagination .pages li button.active{border-color:#f24f44 !important;background-color:#f48565 !important;color:#fff !important
}


